<template>
  <div class="info-container content" @mouseenter="changeRotateFlag(true)" @mouseleave="changeRotateFlag(false)">
    <div class="header">
      <p></p>
    </div>
    <ul class="clear-fix">
      <li>
        <img class="flower-icon" :class="{'rotate-animation':rotateFlag===true}" src="../assets/image/sakura_11.png"/>
        <p></p>
        <p class="f-text">21</p>
        <p>1998/2</p>
      </li>
      <li>
        <img class="flower-icon" :class="{'rotate-animation':rotateFlag===true}" src="../assets/image/sakura_11.png"/>
        <p></p>
        <p class="f-text"></p>
        <p></p>
      </li>
      <li>
        <img class="flower-icon" :class="{'rotate-animation':rotateFlag===true}" src="../assets/image/sakura_11.png"/>
        <p></p>
        <p class="f-text"></p>
        <p></p>
      </li>
      <li>
        <img class="flower-icon" :class="{'rotate-animation':rotateFlag===true}" src="../assets/image/sakura_11.png"/>
        <p></p>
        <p class="f-text"></p>
        <p></p>
      </li>
    </ul>
    <div class="more-info">
      <p></p>
      <p></p>
      <p></p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "InfoPage",
    data() {
      return {
        rotateFlag: 0,
      }
    },
    methods: {
      changeRotateFlag(num) {
        this.rotateFlag = num;
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .info-container {
  }

  ul {
    width: 80%;
    margin: 0 auto;
  }

  ul li {
    width: 25%;
    float: left;
  }

  li p {
    line-height: 2rem;
  }

  .f-text {
    margin-top: 1.5rem;
  }

  .flower-icon {
    width: 35%;
    padding: 0.5rem;
    border-radius: 30%;
    border: 1px solid rgba(255, 255, 255, 0.8);
    transition: border 3s, transform 6s;
  }

  .rotate-animation {
    transform: rotate(270deg);
    border: 0.1rem solid transparent;
  }

  .more-info {
    margin-top: 2rem;
  }

  .more-info p {
    line-height: 2.4rem;
  }
</style>



